.app{text-align:center;display:flex;flex-direction:column;align-items:center;background-image:linear-gradient(to right, #f64668, #ffd1c3);overflow-y:auto}

.flex-center{display:flex;justify-content:center;align-items:center;flex-direction:column}.flex-split{display:flex;justify-content:space-between;align-items:center}.flex-start-row{display:flex;justify-content:flex-start;align-items:center;flex-direction:row}.flex-row{display:flex;flex-direction:row}.flex-col{display:flex;flex-direction:column}.box-shadow{box-shadow:rgba(149,157,165,0.2) 0px 8px 24px}.rounded-corners{border-radius:10px}.fill-container{width:100%;height:100%}.fill-view{width:100vw;height:100vh}

.user-list{width:80vw;padding-top:6rem;display:flex;flex-direction:column;align-items:center;justify-content:center}.user-list a{text-decoration:none}

.user-card{background-color:white;color:#000;width:20rem;margin-bottom:4rem;padding:2rem;text-decoration:none;transition:0.5s all;text-align:left;border-radius:1rem;position:relative}.user-card h2{margin:0;margin-bottom:1rem}.user-card h3{margin:0;margin-bottom:1rem;color:#6a6a6a}.user-card h4{margin:0;color:#6a6a6a}.user-card .tag{color:#fefefe;position:absolute;left:-50px;top:6px}.user-card:hover{box-shadow:rgba(100,100,111,0.2) 0px 7px 29px 0px}

.displayIngredients{background-color:white;color:#000;width:10rem;margin-bottom:4rem;margin-left:2rem;margin-right:2rem;padding:1rem;text-decoration:none;transition:0.5s all;text-align:center;border-radius:1rem;position:relative;display:inline-block}.displayIngredients h2{margin:0;margin-bottom:1rem;font-size:30px}.displayIngredients h3{margin:0;margin-bottom:1rem;color:#6a6a6a}.displayIngredients h4{margin:0;color:#6a6a6a}.displayIngredients .tag{color:#000;position:absolute;left:-50px;top:15px}.display-ingredients:hover{box-shadow:rgba(100,100,111,0.2) 0px 7px 29px 0px}

